<template>

    <div id="ecart2">
      <b-containe fluid>
            <b-container id="ecart">
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <h6>Nombre de gardes réelles du mois:</h6>
                    <h4>{{gardeNombre()}}</h4>
                </b-row>
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col></b-col>
                    <b-col>
                        <div v-for="(garde,index) of gardes" v-bind:key="index">
                            <h5 class="text-left">{{garde.nom}}</h5>
                            <label>Du:<input type="date" v-model="garde.debut"> </label>
                            <label>Au:<input type="date" v-model="garde.fin"> </label>
                        </div>
                    </b-col>
                    <b-col></b-col>
                </b-row>   
                <b-row id="ecart2" align-h="center" class="font-weight-bold">
                    <b-col cols="6" ><b-button block pill variant="success" @click="gardeSave(gardes)">Enregistrer</b-button></b-col>
                    <b-col cols="6"><b-button block pill variant="info" @click="gardeCancel()">Annuler</b-button></b-col>
                </b-row>                                            
            </b-container>  
      </b-containe>  
  </div>
  
</template>

<script>
export default {
    name:'GardeDetail',

    data(){
        return {
            gardes: this.$store.getters.getDetailGarde
        }
    },

    methods: {
        gardeSave(gardes){
            this.$store.commit('setDetailGarde',gardes);
            this.gardeCancel();
        },
        gardeCancel(){
            this.$store.commit('back');
        },
        gardeNombre(){
            return 3;
        }
    }
}
</script>

<style scoped lang="scss">
    #ecart2 {
        margin-top: 2%;
    }
    #ecart {
        margin-top: 5%;
    }
</style>